
.login {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
}

.login-form {
	width: 365px;
	background: #fff;
	padding: 30px;
	border-radius: 5px;
	box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.15);
	position: relative;
}

.loginFormClose {
	top: 10px;
	font-size: 20px;
	right: 15px;
	position: absolute;
	cursor: pointer;
	color: #be2edd;
}
.login-form .title {
	text-align: center;
	font-size: 30px;
	font-weight: 600;
}

form {
	margin-top: 25px;
}

form .input-box {
	height: 50px;
	width: 100%;
	position: relative;
	margin-top: 15px;
}
.input-box input {
	height: 50px;
	width: 100%;
	outline: none;
	font-size: 17px;
	padding-left: 45px;
	border-radius: 5px;
	border: 1px solid lightgrey;
	transition: all 0.3s ease;
}

.input-box input:focus {
	border-color: #be2edd;
}
.input-box .icon {
	position: absolute;
	top: 50%;
	left: 20px;
	color: grey;
	transform: translateY(-50%);
}
form .option_div {
	margin-top: 15px;
	display: flex;
	justify-content: space-between;
}
.option_div .check_box {
	display: flex;
	align-items: center;
}
.option_div span {
	margin-left: 5px;
	font-size: 16px;
	color: #333;
}
.option_div,
.forget_div a {
	font-size: 16px;
	color: #be2edd;
}
.button input {
	color: #fff;
	font-size: 20px;
	font-weight: 500;
	border: none;
	padding-left: 0;
	background: #be2edd;
	cursor: pointer;
	transition: all 0.3s linear;
}

.button input:hover {
	background: #a720c5;
}
form .sign_up {
	text-align: center;
	margin-top: 25px;
}
.sign_up a {
	color: #be2edd;
}
form a {
	text-decoration: none;
}
form a:hover {
	text-decoration: underline;
}


